@include font(avenir, avenir);
@include font(avenir-bold, avenir-bold);
@include font(avenir-demibold, avenir-demibold);
@include font(avenir-medium, avenir-medium);
@include font(opensans-bold, opensans-bold);
@include font(opensans-semibold, opensans-semibold);
@include font(brandon, brandon);
@include font(brandon-medium, brandon-medium);
@include font(brandon-bold, brandon-bold);

@mixin av {
	font-family: 'avenir', sans-serif; }

@mixin av-bold {
	font-family: 'avenir-bold', sans-serif; }

@mixin av-demibold {
	font-family: 'avenir-demibold', sans-serif; }

@mixin av-medium {
	font-family: 'avenir-medium', sans-serif; }

@mixin op-semibold {
	font-family: 'opensans-semibold', sans-serif; }

@mixin op-bold {
	font-family: 'opensans-bold', sans-serif; }

@mixin br {
	font-family: 'brandon', sans-serif; }

@mixin br-medium {
	font-family: 'brandon-medium', sans-serif; }

@mixin br-bold {
	font-family: 'brandon-bold', sans-serif; }
