.dara-category-wrapper {
	padding-top: 40px;
	margin-bottom: 140px;
	@include rmin(1025) {
		margin-bottom: 70px; } }


.dara-category {
	overflow: hidden;
	@include rmin(768) {
		overflow: initial; }
	&__in {
		margin: 0 -15px;
		@include rmin(768) {
			margin: auto; } }
	&__title {
		margin-bottom: 0;
		padding: 2px 20px;
		background: #000;
		color: #FFF;
		letter-spacing: -0.29px; } }


.dara-category-card {
	position: relative;
	display: flex;
	align-items: center;
	padding: 14px 10px 16px;
	color: #000;
	background: #FFF;
	border-bottom: 1px solid #f2f2f2;
	&:hover {
		text-decoration: none;
		color: #000; }
	&:last-child {
		border-bottom: none; }
	&__bl {
		margin: 0 10px;
		&--mob-absolute {
			position: absolute;
			top: 14px;
			right: 5px;
			margin: 0;
			@include rmin(500) {
				position: static; } } }
	&__img {
		display: block;
		max-width: 70px;
		background: #d8d8d8;
		@include rmin(375) {
			max-width: 90px; } }
	&__info {
		@include br;
		font-size: 16px;
		line-height: 1.14; }
	&__title {
		padding-right: 70px;
		margin-bottom: 11px;
		@include op-bold;
		font-size: 14px;
		line-height: 1.2;
		letter-spacing: -0.11px;
		@include rmin(500) {
			padding-right: 0; } } }



.dara-category-proportion {
	@include rmin(500) {
		min-width: 100px;
		margin-left: 30px; }
	&__num {
		@include br-medium;
		font-size: 16px;
		line-height: 1;
		letter-spacing: -0.13px;
		color: #FE0066;
		@include rmin(500) {
			font-size: 18px; }
		.icon {
			vertical-align: -1px;
			@include rmin(500) {
				margin-left: 5px; } } }
	&__add {
		display: none;
		position: relative;
		width: 20px;
		height: 20px;
		margin: -2px 15px 0 0;
		background: #FE0066;
		border-radius: 50%;
		@include rmin(500) {
			margin: -2px auto 0;
			width: 30px;
			height: 30px; }
		&.is-visible {
			display: block; }
		&:before,
		&:after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			border-radius: 5px;
			background: #FFF; }
		&:before {
			width: 2px;
			height: 10px;
			margin: -5px 0 0 -1px;
			@include rmin(500) {
				height: 12px;
				margin-top: -6px; } }
		&:after {
			width: 10px;
			height: 2px;
			margin: -1px 0 0 -5px;
			@include rmin(500) {
				width: 12px;
				margin-left: -6px; } } } }
