.dara-form {
	&__row {
		display: flex;
		margin: 0 -5px 10px;
		&--md-margin-b {
			margin-bottom: 24px; } }
	&__field {
		position: relative;
		flex-basis: 100%;
		margin: 0 5px;
		&.is-active {
			.dara-form__label {
				top: 12px; } }
		&.is-error {
			.dara-form__input,
			.dara-form__select {
				border-color: #E34F30; }
			.dara-form__helper {
				display: block; } } }
	&__label {
		position: absolute;
		top: 50%;
		left: 9px;
		margin: -6px 0 0;
		text-transform: uppercase;
		@include av-demibold;
		font-size: 12px;
		color: #868686;
		line-height: 1;
		letter-spacing: -0.16px;
		transition: top 0.1s linear;
		small {
			font-size: 12px;
			text-transform: none; } }
	&__input {
		display: block;
		width: 100%;
		padding: 23px 9px 24px;
		@include av-demibold;
		font-size: 12px;
		border: 1px solid #ddd;
		line-height: 1;
		letter-spacing: -0.16px;
		transition: border-color 0.2s ease;
		border-radius: 3px; }
	&__helper {
		display: none;
		position: absolute;
		left: 9px;
		bottom: 4px;
		@include av-demibold;
		font-size: 12px;
		color: #E34F30; }
	&__select {
		width: 100%;
		height: 66px;
		padding: 10px 4px;
		@include av-demibold;
		font-size: 12px;
		-webkit-appearance: none;
		background-color: #fff;
		background-image: url(data:image/png;base64,);
		background-position: right center;
		background-repeat: no-repeat;
		border: 1px solid #ddd;
		border-radius: 3px; } }


.dara-field {
	position: relative;
	display: block;
	text-decoration: none;
	margin: 0 -20px;
	padding: 18px 57px 18px 22px;
	@include av-demibold;
	font-size: 12px;
	line-height: 1.33;
	color: #868686;
	border-top: 2px solid #f2f2f2;
	letter-spacing: -0.16px;
	&:hover {
		text-decoration: none;
		color: #868686; }
	&__title {
		text-transform: uppercase;
		@include av-bold;
		color: #000; }
	&__info {
		margin-top: 5px;
		&--flex {
			display: flex;
			max-width: 100%;
			justify-content: space-between; } }
	&__el {
		max-width: 120px; }
	&__arrow {
		position: absolute;
		top: 50%;
		right: 21px;
		margin-top: -10px;
		font-size: 20px; }
	&__icon {
		color: #000;
		font-size: 16px;
		vertical-align: middle;
		line-height: 1; }
	&__cancel {
		margin-left: 14px; } }
